//
// Skin: MatchManao
// Mode: Light
//

$primary:       #000099;
$secondary:     #111E41;
$tertiary:      #4A87AA;
$quartiary:     #a8dadc;
$highlight:     #67f5cb;

$success:       #00CC7E;
$warning:       #F6AE28;
$danger:        #ED6964;

.color-tertiary {
    color: $tertiary !important;
}

.color-highlight {
    color: $highlight !important;
}

.bg-highlight {
    background: $highlight !important;
}

.color-quartiary {
    color: $quartiary;
}

.border-highlight {
    border: 1px solid $highlight !important;
}
